import supabase from "../lib/db";
export const upsertUser = async (user) => {
    if (!user || !user.id)
        return null;
    const prismaUser = {
        aud: user.aud,
        confirmation_sent_at: new Date(user.confirmation_sent_at),
        confirmed_at: new Date(user.confirmed_at),
        created_at: new Date(user.created_at),
        email: user.email,
        email_confirmed_at: new Date(user.email_confirmed_at),
        id: user.id,
        last_sign_in_at: new Date(user.last_sign_in_at),
        phone: user.phone,
        phone_confirmed_at: new Date(user.phone_confirmed_at),
        recovery_sent_at: new Date(user.recovery_sent_at),
        role: user.role,
        updated_at: new Date(user.updated_at),
    };
    console.log('prismaUser', prismaUser);
    try {
        const { data, error } = await supabase
            .from('User')
            .insert([
            Object.assign({}, prismaUser),
        ]).single();
        if (error)
            throw error;
        return data;
    }
    catch (err) {
        console.error(err);
        return null;
    }
};
